import React, {useContext} from "react";
import "./Portfolio.css";
import {Swiper, SwiperSlide} from "swiper/react";
import "swiper/css";
import {themeContext} from "../../Context";
import shop from "../../img/projects/shop.com.png";
import nordstrom from "../../img/projects/nortstrom.png";
import cultfit from "../../img/projects/cultfit1.png";

import javas from "../../img/teckstack/javascript.png";
import react from "../../img/teckstack/react.png";
import css from "../../img/teckstack/icons8-css3-480.png";
import chakra from "../../img/teckstack/icons8-chakra-ui.png";
import shopvideo from "../../img/projects/Shop.com.mp4"
import cultfitvideo from "../../img/projects/cultfit.com.mp4"
import nordstromvideo from "../../img/projects/nordstrom.mp4"
const Projects = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  return (
    <div className="portfolio" id="projects">
      {/* heading */}
      <span style={{color: darkMode ? "white" : ""}}>Recent Proj/ects</span>
      <span>Portfolio</span>

      {/* slider */}
      <Swiper
        spaceBetween={0}
        slidesPerView={2}
        grabCursor={true}
        className="portfolio-slider"
      >
        <SwiperSlide>
          <div className="project-div">
            <div className="img-box">
            <video  variant="top" autoPlay muted loop src={shopvideo}  />
              <div className="hover-div">
                <h1>Description</h1>
                <h2>
                  SHOP.COM is a new kind of shopping comparison site that offers
                  the most comprehensive shopping experience on the web. It is
                  the best collection of the stores you can't live without, and
                  the brands and products you love - all in one place.
                </h2>

                <div className="stack-div">
                  <img src={javas} alt="" />
                  <img src={react} alt="" />
                  <img src={css} alt="" />
                  <img src={chakra} alt="" />
                </div>
              </div>
            </div>

            <div className="bottom-content">
              <div className="link-div">
              <a href="https://github.com/Anantk05/Shop.com-Clone" >
                  <button>Github Link</button>
                </a>
                <a href="https://magnificent-gumption-fc38c2.netlify.app/">
                  <button>Live</button>
                </a>
              </div>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="project-div">
            <div className="img-box">
            <video  variant="top" autoPlay muted loop src={nordstromvideo}  />
              <div className="hover-div">
                <h1>Description</h1>
                <h2>
                  SHOP.COM is a new kind of shopping comparison site that offers
                  the most comprehensive shopping experience on the web. It is
                  the best collection of the stores you can't live without, and
                  the brands and products you love - all in one place.
                </h2>

                <div className="stack-div">
                  <img src={javas} alt="" />
                  <img src={react} alt="" />
                  <img src={css} alt="" />
                  <img src={chakra} alt="" />
                </div>
              </div>
            </div>

            <div className="bottom-content">
              <div className="link-div">
              <a href="https://github.com/itsDipaks/NordStrom.com">
                  <button>Github Link</button>
                </a>
                <a href="https://gilded-axolotl-233746.netlify.app/">
                  <button>Live</button>
                </a>
              </div>
            </div>
          </div>
        </SwiperSlide>
        <SwiperSlide>
          <div className="project-div">
            <div className="img-box">
            <video  variant="top" autoPlay muted loop src={cultfitvideo}  />
              {/* <img src={cultfit} alt="" /> */}
              <div className="hover-div">
                <h1>Description</h1>
                <h2>
                  Fitness megastore platform Cultfit using MERN stack
                </h2>

                <div className="stack-div">
                  <img src={javas} alt="" />
                  <img src={react} alt="" />
                  <img src={css} alt="" />
                  <img src={chakra} alt="" />
                </div>
              </div>
            </div>

            <div className="bottom-content">
              <div className="link-div">
              <a href="https://github.com/Anantk05/Shop.com-Clone" >
                  <button>Github Link</button>
                </a>
                <a href="https://magnificent-gumption-fc38c2.netlify.app/">
                  <button>Live</button>
                </a>
              </div>
            </div>
          </div>
        </SwiperSlide>
      
      </Swiper>
    </div>
  );
};

export default Projects;
