import React, { useContext } from "react";
import "./Works.css";
import github from "../../img/tools/github.png"
import postman from "../../img/tools/postman.png"
import visual from "../../img/tools/visual-studio.png"
import Netlify from "../../img/tools/Netlify-Logo.wine.svg"
import heroku from "../../img/tools/favicon.ico"
import { themeContext } from "../../Context";
import { motion } from "framer-motion";
import {Link} from 'react-scroll'
const Works = () => {
  // context
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  // transition
  return (
    <div className="works" id="works">
      {/* left side */}
      <div className="w-left">
        <div className="awesome1">
          {/* dark Mode */}
          <span style={{ color: darkMode ? "white" : "" }}>
           Tools  I Used
          </span>
          <span> </span>
       
        
          <div
            className="blur s-blur1"
            style={{ background: "#ABF1FF94" }}
          ></div>
        </div>

        {/* right side */}
      </div>
      <div className="w-right">
        <motion.div
          initial={{ rotate: 45 }}
          whileInView={{ rotate: 0 }}
          viewport={{ margin: "-40px" }}
          transition={{ duration: 3.5, type: "spring" }}
          className="w-mainCircle"
        >
          <div className="w-secCircle">
            <img className="imgtools" src={github} alt="" />
          </div>
          <div className="w-secCircle">
            <img className="imgtools" src={postman} alt="" />
          </div>
          <div className="w-secCircle">
            <img className="imgtools" src={visual} alt="" />
          </div>{" "}
          <div className="w-secCircle">
            <img className="imgtools" src={Netlify} alt="" />
          </div>
          <div className="w-secCircle">
            <img className="imgtools" src={heroku} alt="" />
          </div>
        </motion.div>
        {/* background Circles */}
        <div className="w-backCircle blueCircle"></div>
        <div className="w-backCircle yellowCircle"></div>
      </div>
    </div>
  );
};

export default Works;
