import React, {useContext} from "react";
import Card from "../Card/Card";
import HeartEmoji from "../../img/heartemoji.png";
import Glasses from "../../img/glasses.png";
import Humble from "../../img/humble.png";
import {motion} from "framer-motion";
import {themeContext} from "../../Context";
import dev from "../../img/educaton/coding.png"
import be from "../../img/educaton/mech.png"
import diploma from "../../img/educaton/diploma.png"
import "./education.css";
const Education = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  // transition
  const transition = {
    duration: 1,
    type: "spring",
  };
  return (
    < >
      <h1 style={{color: darkMode ? "white" : ""}} className="title" id="educational">
        Educational qualification
      </h1>
      <div className="maincard">
        <div className="cards1">
          {/* first card */}
          <motion.div
            className="firstcard"
            initial={{left: "25rem"}}
            whileInView={{left: "14rem"}}
            transition={transition}
            
          >
            <Card
              emoji={dev}
              heading={"Full Stack Developer"}
              detail={"Masai School"}
              date={"Jan/2022 - Nov/2022"}
            className="card1"
             
              
            
            />
          </motion.div>
          {/* second card */}
          <motion.div
            className="secondcard"
            initial={{left: "-11rem", top: "12rem"}}
            whileInView={{left: "-4rem"}}
            transition={transition}
          >
            <Card
              emoji={be}
              heading={"BE Mechanical Engginearing"}
              detail={"Guru Gobind Singh College Of Engginearing Nashik"}
              date={"Aug/2018 - Nov/2021"}
            />
          </motion.div>
          {/* 3rd */}
          <motion.div
            className="thirdcard"
            initial={{top: "19rem", left: "25rem"}}
            whileInView={{left: "18rem"}}
            transition={transition}
          >
            <Card
              emoji={diploma}
              heading={"Diploma In Mechanical Engginearing"}
              detail={"Amrutvahini Polytechnic Sangamner"}
              color="rgba(252, 166, 31, 0.45)"
              date={"Aug/2014 - Nov/2017"}
            />
          </motion.div>
          <div
            className="blur s-blur2"
            style={{background: "var(--purple)"}}
          ></div>
        </div>
      </div>
    </>
  );
};

export default Education;
