import React, {useContext} from "react";
import {themeContext} from "../../Context";
import "./teckstack.css";
import js from "../../img/teckstack/javascript.png";
import html from "../../img/teckstack/icons8-html-5-480.png";
import react from "../../img/teckstack/react.png";
import redux from "../../img/teckstack/icons8-redux-480.png";
import css from "../../img/teckstack/icons8-css3-480.png";
import chakraui from "../../img/teckstack/icons8-chakra-ui.png"
import bootstrap from "../../img/teckstack/icons8-bootstrap-480.png"
import nodejs from "../../img/teckstack/nodejs-.png"
import express from "../../img/teckstack/express.png"
import mongodb from "../../img/teckstack/icons8-mongodb.png"
const Teckstack = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  return (
    <>
      <h1 className="t-title">Tech-Stack </h1>
      <h1 className="sub-title">FrontEnd Skills</h1>
      <div className="experience" id="teckstack">
     
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--black)" : ""}}>
            <img className="toolsimg" src={js} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Javascript </span>
        </div>
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}>
            <img className="toolsimg" src={html} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Html </span>
        </div>
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={react} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>React </span>
        </div>
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={redux} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Redux </span>
        </div>
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={css} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>css </span>
        </div>
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={chakraui} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Chakra-UI </span>
        </div>
        <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={bootstrap} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Bootstrap </span>
        </div>
       

      </div>
      <h1 className="sub-title">BackEnd Skills</h1>
      <div className="backend">


      <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={nodejs} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Node-Js </span>
        </div>
      <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={express} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Express </span>
        </div>
      <div className="achievement">
          {/* darkMode */}
          <div
            className="circle"
            style={{color: darkMode ? "var(--orange)" : ""}}
          >
            <img className="toolsimg" src={mongodb} />
          </div>
          <span style={{color: darkMode ? "white" : ""}}>Mongo-Db </span>
        </div>
    
        </div>
    </>
  );
};

export default Teckstack;
